import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function netDebtToEbitdaTtm(tableName: string) {
  const title = "Net Debt to EBITDA (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["netDebtToEbitdaTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Debt load compared to earnings.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about net debt to ebitda or other relevant financial measures?`,
              value: info.row.original.financialData.companyKeyMetrics?.data?.netDebtToEbitdaTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyKeyMetrics?.data?.netDebtToEbitdaTtm}
        />
      )
    },
  })
}

export { netDebtToEbitdaTtm }
